body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.test-p {
  color: blue;
}

.profile-error {
  margin-top: 15px;
}

.profile-success {
  margin-top: 15px;
}

.page-container{
  padding-left: 0px;
  padding-right: 0px;
  overflow-x:hidden;
}
.login-container {
  display: -webkit-flex;
  display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 150px;
    margin-bottom: 50%;
    max-width: 700px;
}

.food-list-form {
  margin-top: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.food-list-form-category{
  width: 30%
}

.food-list-form-sort {
  margin-left: 15px;
  width: 50%;
}

.diet-page-content-wrapper {
  padding-left: 15px;
  padding-right: 15px;
}

.food-list-content-wrapper {
  padding-left: 15px;
  padding-right: 15px;
}
.profile-page-form-wrapper {
  padding-top: 15px;
  padding-left: 15px;
  padding-right: 15px;
  -webkit-justify-content: center;
          justify-content: center;
}

.profile-page-form{
  display: -webkit-flex;
  display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.welcome-page-content{
  margin-top: 150px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center
}

.profile-page-form-group {
  -webkit-align-items: center;
          align-items: center;
}

.react-bs-table-tool-bar  > div div {
  padding-top: 0px;
  -webkit-flex: 0 0 100%;
          flex: 0 0 100%;
  max-width: 100%;
}

.meal-date-control-wrapper {
  padding-top: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.meal-date-control-form {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.range-date-control{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
 width: 100%;
}

.meal-btn-group {
  margin-bottom: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.add-edit-meal-btn {
  margin-right: 15px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

